import {Link, Navigate} from "react-router-dom";
import {useAppDispatch, useAppSelector} from "../../../store/hooks";
import {LogoutTC} from "../../../store/reducers/auth-reducer";
import classPersonal from "../personalArea.module.css"


export const PersonalNavBar = () => {
    const dispatch = useAppDispatch()
    const profile = useAppSelector(state => state.profile.profile)

    const logout = () => {
        dispatch(LogoutTC())
    }
    if (!profile.id){
        return <Navigate to={'/'}/>
    }
    return (
            <div className={classPersonal.personalNav}>
                <div><Link to={'favorites'}></Link></div>
                <div><Link to={'orders'}></Link></div>
                <div><Link to={'reviews'}></Link></div>
                <div><Link to={'start-selling'}> </Link></div>
                <div><Link to={'download-goods'}> </Link></div>
                <div><Link to={'material-warehouse'}>  </Link></div>
                <div><Link to={'finished-products-warehouse'}>  </Link></div>
                <div><Link to={'purchases'}>  </Link></div>
                <div><Link to={'ledger'}>   </Link></div>
                <div onClick={logout}></div>
            </div>
    )
}
